.backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    transition: opacity .3s, visibility .3s;

    &.is-hidden {
        opacity: 0;
        visibility: hidden;
    }
}

.modal {
    position: relative;
    width: 100%;
    max-width: 880px;
    height: 100%;
    max-height: 880px;
    padding: 48px;
    background-color: var(--color-primary-light);
    border-radius: 32px;
    overflow-y: auto;

    @media screen and (min-width: $bp-tablet) {
        padding: 128px;
    }

    &__btn-close {
        position: absolute;
        top: 40px;
        right: 40px;
    }

    &__form-field {
        margin-bottom: 32px;
    }

    &__button {
        margin: 0 auto 24px;
    }
}