.header {
    position: absolute;
    top: 16px;
    left: 0;
    width: 100%;

    @media screen and (min-width: $bp-desktop) {
        top: 32px;
    }

    &__container {
        display: flex;
        align-items: center;
        gap: 40px;
    }

    &__nav {
        display: flex;
        align-items: center;
        flex-grow: 1;
        gap: 40px;
    }

    &__menu {
        display: none;


        @media screen and (min-width: $bp-desktop) {
            display: flex;
            gap: 64px;
            flex-grow: 1;
            justify-content: center;
        }
    }

    &__button {
        display: none;

        @media screen and (min-width: $bp-desktop) {
            display: block;
        }
    }

    &__burger {
        display: block;
        width: 24px;
        height: 24px;
        padding: 0;
        background-color: transparent;
        border: none;
        fill: var(--color-primary-light);

        @media screen and (min-width: $bp-desktop) {
            display: none;
        }
    }
}